<template>
  <div class="page-resume-family">
    <p class="resume-block-title"></p>
    <el-form label-width="80px" label-position="left">
      <div class="form-item-group"
        v-for="(item, index) in familyMembers"
        :key="index">
        <div class="item-block">
          <el-form-item label="">
            <el-input v-model="item.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="item.relation" placeholder=""></el-input>
          </el-form-item>
        </div>

         <div class="item-block">
          <el-form-item label="">
            <el-input v-model="item.age" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="item.address" placeholder=""></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
            <el-input v-model="item.workPlace" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="item.position" placeholder=""></el-input>
          </el-form-item>
        </div>
      </div>
      <div class="item-block item-add">
        <el-button @click="addMembers" icon="el-icon-plus"></el-button>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left"></el-button>
        <el-button type="primary"><i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        familyMembers: [{
          name: '',
          relation: '',
          age: '',
          address: '',
          workPlace: '',
          position: ''
        }]
      }
    },
    methods: {
      addMembers () {
        this.familyMembers.push({
          name: '',
          relation: '',
          age: '',
          address: '',
          workPlace: '',
          position: ''
        })
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-family
    padding-bottom: 50px
</style>



