<style lang="stylus">
  .content-menu
    position: fixed
    top: 100px
    width: 150px
    height: 100%
    padding: 0 25px
    .el-button
      margin-bottom: 35px
  .user-content-route
    padding-left: 200px
    padding-top: 50px
    padding-right: 50px
</style>



