<template>
  <div class="page-resume-work">
    <p class="resume-block-title">5</p>
    <el-form label-width="85px" label-position="left">
      <div class="form-item-group"
        v-for="(item, index) in workList" :key="index">
        <div class="item-block">
          <el-form-item label="">
            <el-date-picker
              v-model="item.startDate"
              type="date"
              placeholder=""
              :picker-options="pickerOptions0">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="item.endDate"
              type="date"
              placeholder=""
              >
            </el-date-picker>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
            <el-input v-model="item.workPlace" placeholder=""></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="item.position" placeholder=""></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
            <el-input v-model="item.salary" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="item.leaveReason" placeholder=""></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
            <el-input v-model="item.proveName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="item.proveTel" placeholder=""></el-input>
          </el-form-item>
        </div>
      </div>
      <div class="item-block item-add">
        <el-button @click="addList" icon="el-icon-plus"></el-button>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left"></el-button>
        <el-button type="primary"><i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        workList: [{
          startDate: '',
          endDate: '',
          workPlace: '',
          position: '',
          salary: '',
          leaveReason: '',
          proveName: '',
          proveTel: ''
        }],
        workOptions: [{
          label: '',
          value: 0
        }, {
          label: '',
          value: 1
        }, {
          label: '',
          value: 2
        }, {
          label: '',
          value: 3
        }, {
          label: '',
          value: 4
        }, {
          label: '',
          value: 5
        }]
      }
    },
    methods: {
      addList () {
        this.workList.push({
          startDate: '',
          endDate: '',
          workPlace: '',
          position: '',
          salary: '',
          leaveReason: '',
          proveName: '',
          proveTel: ''
        })
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-work
    padding-bottom: 50px
</style>



