<template>
  <div class="page-resume-baseinfo">
    <el-form label-width="80px" label-position="left">
      <div class="item-block">
        <el-form-item label="">
          <el-select v-model="value" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="value" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="">
          <el-date-picker
            v-model="workVal"
            type="date"
            placeholder=""
            :picker-options="pickerOptions0">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-input placeholder=""></el-input>
        </el-form-item>
      </div>

      <el-form-item label="">
        <el-checkbox-group v-model="checkList">
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
    </el-form>

    <div class="baseinfo-block">
      <p class="resume-block-title"></p>
      <el-form label-width="80px" label-position="left">
        <div class="item-block">
          <el-form-item label="">
            <el-input placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="genderVal">
              <el-option v-for="(item, index) in genderOptions"
                :key="index" :label="item.label"
                :value="item.value"></el-option>
            </el-select>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
            <el-date-picker
              v-model="birthVal"
              type="date"
              placeholder=""
              :picker-options="pickerOptions0">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
           <el-input placeholder=""></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
           <el-input placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
           <el-input placeholder=""></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
           <el-input placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="politicsVal">
              <el-option v-for="(item, index) in politicsOptions"
                :key="index" :label="item.label"
                :value="item.value"></el-option>
            </el-select>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
            <el-checkbox-group v-model="checkMarriedList">
              <el-checkbox label=""></el-checkbox>
              <el-checkbox label=""></el-checkbox>
              <el-checkbox label=""></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="" label-width="120px">
            <el-checkbox-group v-model="checkMilitaryList">
              <el-checkbox label=""></el-checkbox>
              <el-checkbox label=""></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </div>
      </el-form>
      <div class="item-block btn-container">
        <el-button type="primary"></el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import VHeader from './header'

  export default {
    data () {
      return {
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        value: '',
        pickerOptions0: {
          disabledDate (time) {
            return time.getTime() > Date.now()
          }
        },
        birthVal: '',
        checkList: ['', ''],
        genderOptions: [{
          label: '',
          value: 0
        }, {
          label: '',
          value: 1
        }],
        genderVal: '',
        workVal: '',
        politicsOptions: [{
          label: '',
          value: 1
        }, {
          label: '',
          value: 2
        }, {
          label: '',
          value: 3
        }],
        politicsVal: '',
        checkMarriedList: [''],
        checkMilitaryList: ['']
      }
    },
    components: { VHeader }
  }
</script>

<style lang="stylus">
.page-resume-baseinfo
  padding-bottom: 50px
</style>



