<template>
  <div class="page-resume-technology">
    <p class="resume-block-title"></p>
    <el-form>
      <el-form-item label="?EXCEL,WORD,PPT">
        <el-checkbox-group v-model="checkList">
          <el-checkbox label="EXCEL"></el-checkbox>
          <el-checkbox label="WORD"></el-checkbox>
          <el-checkbox label="PPT"></el-checkbox>
        </el-checkbox-group>
        <el-input type="textarea" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label=" ?">
        <el-input :autosize="textareaSize" type="textarea" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label=" / ">
        <el-input :autosize="textareaSize" type="textarea" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input :autosize="textareaSize" type="textarea" placeholder=""></el-input>
      </el-form-item>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left"></el-button>
        <el-button type="primary"><i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        checkList: [],
        textareaSize: { minRows: 5, maxRows: 8 },
        technologyOptions: [{
          label: '',
          value: 0
        }, {
          label: '',
          value: 1
        }, {
          label: '',
          value: 2
        }, {
          label: '',
          value: 3
        }, {
          label: '',
          value: 4
        }, {
          label: '',
          value: 5
        }]
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-technology
    padding-bottom: 50px
</style>



