<template>
  <el-form class="user-form-card" label-width="80px">
    <div class="item-block">
      <el-form-item label="">
        <el-input v-model="name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="position">
          <el-option v-for="(item, index) in positionOptions"
            :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="position">
          <el-option v-for="(item, index) in positionOptions"
            :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="form-item-date"
        label="" label-width="100px">
        <el-date-picker
          v-model="resumeDate"
          type="daterange"
          range-separator="-"
          start-placeholder=""
          end-placeholder="">
        </el-date-picker>
      </el-form-item>
    </div>

    <div class="item-block">
      <el-form-item label="">
        <el-checkbox-group v-model="checkGenderList">
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="">
        <el-checkbox-group v-model="checkMarriedList">
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item class="form-item-date"
        label="" label-width="120px">
        <el-date-picker
          v-model="birthDate"
          type="daterange"
          range-separator="-"
          start-placeholder=""
          end-placeholder="">
        </el-date-picker>
      </el-form-item>
    </div>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left"></el-button>
        <el-button><i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </el-form>
</template>

<script>
  export default {
    data () {
      return {
        name: '',
        position: '',
        resumeDate: '',
        birthDate: '',
        checkGenderList: [''],
        checkMarriedList: [],
        positionOptions: [{
          label: '',
          value: 1
        }, {
          label: 'Java',
          value: 2
        }]
      }
    }
  }
</script>

<style lang="stylus">
  .user-form-card
    border: 1px solid #ddd
    padding: 10px 15px
    margin-bottom: 35px
    .item-block
      display: flex
      &.btn-container
        justify-content: center
      .el-form-item
        flex: 1
    .el-input
      width: 125px
    .el-date-editor--daterange.el-input__inner
      width: 180px
</style>



